import {
	MESSAGE_FETCH,
  MESSAGE_REQUEST,
  MESSAGE_STOP_FETCH
} from '../actions/actionTypes';

const initialState = {
	message: '',
	isLoading: false
};

export default function stargazers(state = initialState, action) {
	switch (action.type) {
	case MESSAGE_FETCH:
		return Object.assign({}, state, {
	        message: state.message
	    })
	case MESSAGE_REQUEST:
		return Object.assign({}, state, {
			isLoading: true
		})
	case MESSAGE_STOP_FETCH:
		return Object.assign({}, state, {
			isLoading: false
		})
	default:
		return state;
	}
}



